
.header {
  padding: 0 12px 0 12px;
  background-color: rgba(17,27,37,0.1);
}
.header .roomInfo {
  text-align: center;
  color: #f00;
  font-size: 24px;
}
.header .playersInfo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.header .playersInfo .user {
  background-color: #b5d592;
  width: 160px;
  padding: 25px;
  border: 1px solid #404;
}
.header .playersInfo .user .userName {
  color: #3a33d1;
}
.header .playersInfo .user .avatar img {
  width: 100px;
  border-radius: 50px;
}
.header .playersInfo .players {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 12px 12px 12px 12px;
  border: 1px solid #404;
  text-align: center;
  line-height: 40px;
  background-color: #fc8c84;
}
.header .playersInfo .players .player {
  display: inline-block;
  text-align: center;
  width: 80px;
  overflow: hidden;
}
.header .playersInfo .players .player .userName {
  width: 80px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.header .playersInfo .players .player .avatar img {
  width: 50px;
  border-radius: 25px;
}


